
































import util from '@/common/util'
import navigate from '@/components/navigator'
import cities from '@/common/maoyan-cities'
import navscroll from '@/components/nav-scroll'

export default {
    data() {
        return {
            isShowBack:true,
            movieId:'',
            titleText:'',
            showStar:false,
            showShare:false,     
            showSearchIcon:true,          
            pageIndex:1,
            totalCount:0,
            loadingText:'',
            commentList:[],
            loadAll:false,
            totalPage:0,
            cId:''
        }
    },
    onLoad(option) {
        let self = this;
        self.titleText = option.movieName;
    },
    onReady(){
        let self = this;
        self.init();
    },
    onPullDownRefresh(){
        let self = this;
        self.onPullDownRefresh();
    },
    onReachBottom(){
        let self = this;
        self.pageIndex++;
        if(!self.loadAll){
            self.loadMore();
        }
    },
    methods: {
        init(){
            //获取短评
            let self = this;

            //定位
            uni.getLocation({
                type: 'wgs84',
                success: function (res) {
                    //坐标转换 wgs84 => bd09 
                    var cjo2 = util.wgs84togcj02(res.longitude,res.latitude);
                    var bd09 = util.gcj02tobd09(cjo2[0],cjo2[1]);

                    //获取当前城市信息
                    self.api.getCityInfo({
                        lat:bd09[1],
                        lng:bd09[0]
                    }).then(res=>{
                        cities.data.forEach(function(item,index){
                            if(res.result.addressComponent.city.indexOf(item.name) != -1){
                                self.cId = item.id; 
                            }
                        })
                    }).then(()=>{
                        //过滤影院
                        self.api.filterCinemas({
                            data:{
                                cid:self.cId
                            }
                        }).then(res=>{
                            console.log('cinemas:',res);
                        })                            
                    });
                }
            });               
        },
        loadMore(){
            //加载更多
            let self = this;
            self.api.getMinComents({
                data:{
                    pageIndex:self.pageIndex,
                    movieId:self.movieId                        
                }
            }).then(res=>{
                if(res.data.cts.length <= 0){
                    self.loadAll = true;
                    self.pageIndex = 1;
                    self.loadingText = '已加载全部';
                }else{
                    res.data.cts.forEach((item,index)=>{
                        item.date = util.formatDate('Y-m-d H:i:s',item.cd);
                    })
                    self.commentList = self.commentList.concat(res.data.cts);;
                    self.loadingText = '加载中...';                        
                }
            }).catch(rej=>{
                rejected();
            })   
        },
        onPullDownRefresh(){
            this.init().then(()=>{
                uni.stopPullDownRefresh();
            })
        }
    },
    components:{
        navigate,
        navscroll
    }
}
